@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700&subset=latin-ext);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

#root {
  height: 100vh;
  width: 100vw; }

html {
  height: 100%;
  scroll-behavior: smooth; }

body {
  color: #ccc;
  font-family: 'Roboto', sans-serif;
  max-height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/SpaceXFactsSince2006/static/media/space.dcc9a686.jpg);
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #000; }

.selectYears {
  position: absolute;
  background-color: transparent;
  color: white;
  text-align-last: center;
  padding: 10px;
  width: 200px;
  margin-top: 20px;
  font-size: 25px;
  scrollbar-face-color: #000000;
  scrollbar-shadow-color: #2D2C4D;
  scrollbar-highlight-color: #7D7E94;
  scrollbar-3dlight-color: #7D7E94;
  scrollbar-darkshadow-color: #2D2C4D;
  scrollbar-track-color: #7D7E94;
  scrollbar-arrow-color: red; }

option {
  background-color: black; }

.mainHeader {
  font-size: 40px;
  color: white;
  position: absolute;
  margin-bottom: 150px; }

hr {
  margin-bottom: 100px;
  position: absolute; }

:focus {
  outline: none; }

::-webkit-scrollbar {
  display: none; }

.wrapper {
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat; }
  .wrapper .fetchdata {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
    .wrapper .fetchdata input {
      background: none;
      border-width: 0px;
      border: none;
      padding: 7px 0;
      border-bottom: 3px solid #ccc;
      color: #ccc;
      font-size: 30px;
      text-align: center;
      letter-spacing: 1px;
      margin: 1em;
      font-family: 'Roboto', sans-serif; }
    .wrapper .fetchdata .fetchdata__item {
      background: rgba(27, 33, 35, 0.1);
      padding: 1em; }

.modal {
  height: 100%;
  width: 100%; }

.modal__container {
  display: flex;
  color: #000;
  overflow: hidden;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%; }
  .modal__container img {
    height: 100%;
    width: 30%;
    margin: 1em; }
  .modal__container .modal__imagecontainer {
    height: 100%;
    width: 50%;
    display: flex;
    align-items: center; }
  .modal__container .modal__textcontainer {
    display: flex;
    flex-direction: column; }

.ReactModal__Content {
  opacity: 0; }

.ReactModal__Content--after-open {
  opacity: 1;
  transition: opacity 1000ms;
  top: 20% !important;
  left: 20% !important;
  right: 20% !important;
  bottom: 20% !important; }

.ReactModal__Content--before-close {
  opacity: 0; }

.data__container {
  margin-top: 350px;
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 50%; }

@media screen and (max-width: 768px) {
  .modal__container {
    flex-direction: column;
    overflow: visible;
    overflow: initial; }
    .modal__container img {
      width: 100%; }
  .ReactModal__Overlay {
    top: 3% !important;
    left: 3% !important;
    right: 3% !important;
    bottom: 3% !important; }
  .ReactModal__Content--after-open {
    top: 3% !important;
    left: 3% !important;
    right: 3% !important;
    bottom: 3% !important; }
  .data__container {
    width: 100%; } }

